@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
*,*::after,*::before{margin:0;padding:0;box-sizing:border-box}html{font-size:62.5%}body{background-color:#f5f5f5;color:#333;font-family:"Poppins", sans-serif}

.scroll{height:100vh;position:fixed;top:0;right:0;bottom:0;left:0;background-color:rgba(255,255,255,0.4)}.scroll .loader{position:absolute;top:50%;left:50%;transform:translateX(-50%)}.load{height:50vh}

.form-container{background:linear-gradient(to right, #232526, #414345);padding:5rem 2rem 2rem 2rem;letter-spacing:1px}.form{margin:0 auto;max-width:114rem;color:#f7f7f7}.form-input{display:block;margin:0 auto;padding:1.5rem 5rem;font-size:1.5rem;font-family:inherit;letter-spacing:inherit;color:inherit;border:none;border-radius:10rem;width:90%;background-color:rgba(255,255,255,0.5);transition:all 0.3s}.form-input:focus{outline:none}.form-input::-webkit-input-placeholder{color:#eee}.form-label{color:inherit;font-size:1.4rem;font-weight:400;display:block;margin-left:11rem;margin-bottom:0.7rem;transition:all 0.3s;transform:translateY(-8rem)}.form-input:-ms-input-placeholder+.form-label{opacity:0;visibility:hidden;transform:translateY(-4rem)}.form-input:placeholder-shown+.form-label{opacity:0;visibility:hidden;transform:translateY(-4rem)}@media only screen and (max-width: 1200px){.form-container{padding:5rem 1.5rem 1.5rem 1.5rem}.form-input{margin:0 auto}.form-label{margin-left:10.5rem}}@media only screen and (max-width: 1000px){.form-label{margin-left:10rem}}@media only screen and (max-width: 800px){.form-label{margin-left:9rem}}@media only screen and (max-width: 650px){.form-label{margin-left:8rem}}@media only screen and (max-width: 500px){.form-label{margin-left:7rem}}@media only screen and (max-width: 350px){.form-input{padding:1rem 3rem}.form-label{margin-left:4rem}}

.image-container{margin:5rem auto 0 auto;max-width:90%;display:grid;grid-template-columns:repeat(auto-fill, minmax(40rem, 1fr));grid-column-gap:1.5rem;-webkit-column-gap:1.5rem;column-gap:1.5rem;grid-auto-rows:1.5rem}@media only screen and (max-width: 500px){.image-container{margin:3rem auto 0 auto;grid-template-columns:36rem}}@media only screen and (max-width: 400px){.image-container{grid-template-columns:32rem}}@media only screen and (max-width: 350px){.image-container{grid-template-columns:28rem}}@media only screen and (max-width: 280px){.image-container{grid-template-columns:24rem}}

.image{width:100%;object-fit:contain}@media only screen and (max-width: 1360px){.image{margin-bottom:1rem}}@media only screen and (max-width: 400px){.image{margin-bottom:0}}

