.scroll {
    height: 100vh;
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-color: rgba(#fff, 0.4);
    .loader {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translateX(-50%);
    }
}

.load {
    height: 50vh;
}
